body {
  margin: 0px;
}


.content {
  margin: 0;
  padding-top: 30px;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: space-evenly;
  justify-items: center;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  /* padding-bottom: 5px; */
  padding-top: 5px;
  font-family: 'Franklin Gothic', 'Arial', sans-serif;
  font-size: 10pt;
  display: flex;
  flex-direction: column;
  background-color: #001489;
  color: #DDBB00;
}

.footer p{
  margin-top: 5px;
  padding-top: 0px;
}

.vote-button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
  border: 2px solid #001489;
  margin: 1rem;
  height: 4rem;
  width: 10rem;
  background-color: #FFDD00;
  color: #001489; 
  font-family: 'Franklin Gothic', 'Arial', sans-serif;
  font-size: 18pt;
  cursor: pointer;
}

.vote-button.disabled {
  color: gray;
  background-color: darkgray;
  border-color: gray;
  cursor: default;
}

.item-pair {
  padding-top: 20px;
  width: 100%;
  max-width: 1100px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.item-list {
  padding-top: 20px;
  padding-bottom: 4rem;
  width: 100%;
  max-width: 1100px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.item-description-box {
  font-size: medium;
  border: 1px solid;
  border-radius: 5px;
  padding: 15px;
  background-color: #FFDD00;
  color: #001489;
  font-family: "Helvetica", "Arial", sans-serif;
  white-space: pre-line
}

.item-container {
  margin-top: 20px;
  width: 50%;
  max-width: 400px; /* Adjust this value as needed */
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Helvetica", "Arial", sans-serif;
  background-color: #001489;
  color: #DDBB00;
  transition: box-shadow 0.3s ease;
}

.item-container.selected {
  box-shadow: 0 0 2rem rgba(0, 0, 0, 0.5); /* Adjust the shadow as needed */
  background-color: #FFCC00;
  color: #001489;
}

.item-container img {
  width: auto;
  height: 100%;
  max-height: 50vh;
  border-radius: 10px;
}

.expand-button {
  background-color: darkgreen;
}

.navbar-left {
  background-color: #001489;
  color: white;
  width: 50%;
  height: 10vh;
  max-height: 8rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}

.navbar-right {
  background-color: #001489;
  color: white;
  width: 50%;
  height: 10vh;
  max-height: 8rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.navbar-link-left {
  color: #DDBB00;
  font-size: 20px;
  text-decoration: none;
  display: flex;
  align-items: center;
  font-family: 'Franklin Gothic', 'Arial', sans-serif;
  font-size: 30pt;
}

.navbar-link-left:visited {
  color: #DDBB00;
}

.navbar-link-right {
  color: #DDBB00;
  font-size: 20px;
  text-decoration: none;
  display: flex;
  align-items: center;
  font-family: 'Franklin Gothic', 'Arial', sans-serif;
  font-size: 30pt;
}

.navbar-link-right:visited {
  color: #DDBB00;
}

.navbar-list {
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin-bottom: 2rem;
}

.navbar-item {
  margin: 0 10px;
  padding-left: 4rem;
  padding-right: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.total-votes-text {
  padding-bottom: 1.0rem;
  font-size: 12pt;
  font-family: 'Franklin Gothic', 'Arial', sans-serif;
  color: #222222;
}

.home-page-text {
  padding-top: 1rem;
  font-size: 50pt;
  font-family: 'Franklin Gothic', 'Arial', sans-serif;
  font-weight: bold;
  color: #222222;
}
